<template>
  <div class="login-container">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center">江海大二手交易平台</div>
      <div slot="right"></div>
    </nav-bar>
    <el-card
    class="m-2 login-card text-center fs-xl">
      <!-- ative表示监听表单的原生事件，prevent表示阻止默认跳转 -->
      <el-form :rules="rules" @submit.native.prevent="login" ref="formRef" :model="model">
        <h2 class="text-center text-primary pb-2">用 户 登 录</h2>
        <el-form-item prop="username" label="用户名">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item class="pt-3">
          <el-button type="primary" native-type="submit">登录</el-button>
          <el-button @click="resetForm('formRef')">重置</el-button>
        </el-form-item>
        <el-form-item>
          <span>没有账号？ <router-link class="text-primary" tag='span' to="/register"> 立即注册</router-link></span>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  components: {
    NavBar
  },
  data(){
    return{
      model:{},
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ],
      }
    }
  },
  methods:{
    async login(){
      const res = await this.$http.post('login',this.model)
      // console.log(res.data)
      localStorage.token = res.data.token // 将服务器返回的token保存到本地
      this.$router.push('/')
      this.$message({
        type:'success',
        message:'登录成功'
      })
    },
    resetForm(formRef) {
      this.$refs[formRef].resetFields();
    }
  }
}
</script>

<style>
  .login-card{
    margin: 3rem auto;
    height:100vh;
    z-index: 999;
  }
</style>