<template>
  <div class="my-demand">
    <router-link tag="div" 
    :to="`/demand/edit/${item._id}`"
    v-for="item in model" class="mb-2">
      <el-card class="p-2">
        <h3>{{item.name}}</h3>
        <div>{{item.description}}</div>
        <div class="price mt-3">￥{{item.price}}</div>
      </el-card>
    </router-link>
    <div class="height"></div>
  </div>
</template>

<script>
export default {
  props:{
    id:{
      type:String,
      required:true
    }
  },
  data(){
    return{
      model:[],
    }
  },
  methods:{
    async fetch(){
      const res = await this.$http.get(`/demands/${this.id}`)
      this.model = res.data
      // console.log(this.model)
    }
  },
  created(){
    this.fetch()
  }
}
</script>

