import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Release from '../views/Release.vue'
import Demand from '../views/Demand.vue'
import Me from '../views/Me.vue'
import Square from '../views/Square.vue'
import Login from '../views/Login.vue'
import Register from '../views/Register.vue'
import Good from '../views/Good.vue'
import UserInfo from '../views/UserInfo.vue'
import Setting from '../views/Setting.vue'
import Avatar from '../views/Avatar.vue'

Vue.use(VueRouter)

const routes = [
    { path: '/', name: 'Home', component: Home, meta: { isPublic: true } },
    { path: '/release', name: 'Release', component: Release },
    { path: '/release/edit/:id', name: 'Release1', component: Release, props: true },
    { path: '/square', name: 'Square', component: Square, meta: { isPublic: true } },
    { path: '/demand', name: 'Demand', component: Demand },
    { path: '/demand/edit/:id', name: 'Demand1', component: Demand, props: true },
    { path: '/me', name: 'Me', component: Me, meta: { isPublic: true } },
    { path: '/setting', name: 'Setting', component: Setting },
    { path: '/avatar/:id', name: 'Avatar', component: Avatar, props: true },
    { path: '/userinfo/:id', name: 'UserInfo', component: UserInfo, props: true },
    { path: '/login', name: 'Login', component: Login, meta: { isPublic: true } },
    { path: '/register', name: 'Register', component: Register, meta: { isPublic: true } },
    { path: '/goods/:id', name: 'Good', component: Good, props: true, meta: { isPublic: true } },
]
const router = new VueRouter({
    routes
})

// 导航守卫
router.beforeEach((to, from, next) => {
    // 如果要访问的路由不是公开的并且没有携带token，就跳转到登录页
    if (!to.meta.isPublic && !localStorage.token) {
        Vue.prototype.$message({
            type: 'error',
            message: '请先登录！'
        })
        return next('/login')
    }
    next()
})


export default router