        <h3 class="text-space">{{model.name}}</h3>
        <div class="text-space text-grey-1 mb-2">{{model.description}}</div>
        <div class="mt-3 d-flex pt-3 pb-3">
          <span class="price fs-xl mr-2">￥{{model.price}}</span>
          <del class="text-grey-1 flex-1">￥{{model.originalPrice}}</del>
          <span class="text-grey-1 mr-3 pr-3" v-for="item in model.type"> 
            <i class="el-icon-collection-tag"></i>
