            <el-dropdown-item command="商品已售出">商品已售出</el-dropdown-item>
            <el-dropdown-item command="其他">其他</el-dropdown-item>
            <!-- <el-dropdown-item command="自定义举报内容" divided>自定义举报内容</el-dropdown-item> -->
          </el-dropdown-menu>
        </el-dropdown>
