<script>
import Swiper from '../components/Swiper'
import NavBar from '../components/NavBar'
export default {
  name: 'Good',
  components: {
    Swiper,
    NavBar
  },
  props:{
    id:{required:true}
  },
  data(){
    return{
      model:{
        photo:[],
        price:0,
        originalPrice:0,
        description:'',
        userId:{ avatar:'' }
     },
      autoplay:false,
      report:{
        reportID:'',
        reportContent:''
      }
    }
  },
  computed:{
    isLogin(){
      return localStorage.token ? true : false
    }
  },
  methods:{
    async fetch(){
      const res = await this.$http.get(`/goods/${this.id}`)
      this.model = res.data
    },
    async handleCommand(command) {
      this.report.reportID = this.model.userId
      this.report.reportContent = command
      const res = await this.$http.post('/rest/reports',this.report)
      this.$message({
        message: "举报成功，我们会及时处理",
        type:'success'
      });
    }
  },
  created(){
    this.fetch()
  }
}
</script>

