<template>
  <div class="avatar">
    <nav-bar>
      <router-link tag="div" to="" slot="left"><span @click="$router.back(-1)">取消</span></router-link>
      <div slot="center">修改头像</div>
      <div slot="right"></div>
    </nav-bar>
    <div style="height:6rem;"></div>
    <div class="d-flex flex-column text-center">
      <el-upload
      class="mb-3"
      multiple
      :action="uploadUrl"
      :headers="getAuthHeaders()"
      list-type="picture-card"
      :show-file-list="false"
      :on-preview="handlePictureCardPreview"
      :on-success="afterUpload"
      >
        <img v-if="imgUrl" :src="imgUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <div class="m-2 text-grey-1">建议上传头像图片尺寸为 1:1</div>
      <div><el-button type="primary" class="m-3" @click="updateAvatar">确定修改</el-button></div>
    </div>

  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  props:{
    id:{required:true}
  },
  data(){
    return {
      imgUrl:'',
      model:{
        avatar:'',
      }
    }
  },
  components: {
    NavBar
  },
  methods: {
    // 将后台返回的图片地址保存下来
    async afterUpload(res){
      this.imgUrl = await res.url
      this.model.avatar = this.imgUrl
    },
    // 展示上传的图片
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    async updateAvatar(){
      console.log(this.model)
      const res = await this.$http.put(`/rest/users/${this.id}`,this.model)
      this.$router.go(-1); 
      this.$message({
        type:'success',
        message:'修改成功'
      })
    },
    async fetchUser(){
      const res = await this.$http.get(`/rest/users/${this.id}`)
      this.model = res.data
    }
  },
  created(){
    this.fetchUser()
  }
}
</script>

