<template>
  <div class="demand">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center">江海大二手交易平台</div>
      <div slot="right"></div>
    </nav-bar>
    <el-form class="bg-white auto-hight m-2 p-2" 
      style="height:90vh;"
      ref="formRef"
      :model="model" 
      label-width="80px"
      @submit.native.prevent="save">
        <h2 class="text-center text-primary p-3">{{id ? "编 辑" : "发 布"}} 求 购 信 息</h2>
        <el-form-item prop="name" label="物品名称">
          <el-input placeholder="很重要，让别人对您的需求一目了然" v-model="model.name"></el-input>
        </el-form-item>
        <!-- <el-form-item prop="campus" label="所在校区">
          <el-select v-model="model.campus" placeholder="请选择您所在的校区">
            <el-option label="宋跳校区" value=0></el-option>
            <el-option label="苍梧校区" value=1></el-option>
            <el-option label="通灌校区" value=2></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item prop="description" label="物品描述">
          <el-input type="textarea" 
          :rows="8" 
          v-model="model.description"
          placeholder="让别人更清晰了解您的需求"></el-input>
        </el-form-item>
        <el-form-item prop="tel" label="联系方式">
          <el-input placeholder="让别人更加方便地联系您"  v-model="model.tel"></el-input>
        </el-form-item>
        <el-form-item prop="price" label="预期价格">
          <el-input placeholder="数字或文字，如'100-500之间'" v-model="model.price"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" native-type="submit">{{id ? "保存" : "发布"}}</el-button>
          <el-button size="small" type="warning" @click="resetForm('formRef')">重置</el-button>
          <el-button v-if="id" size="small" type="danger" @click="deleteDemandInfo">删除</el-button>
          <el-button v-if="id" size="small" type="info" @click="$router.back(-1)">取消</el-button>
        </el-form-item>
    </el-form>
    <!-- <div class="height"></div> -->
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  name: 'Demanfd',
  components: {
    NavBar
  },
  props:{
    id:{
      type:String
    }
  },
  data(){
    return {
      model: {
          name: '',
          campus: '',
          description: '',
          price: '',
          tel: '',
        },
        dialogImageUrl: '',
        dialogVisible: false,
        types:null,
    }
  },
  methods:{
    // 重置表单
    resetForm(formRef) {
      this.$refs[formRef].resetFields();
    },
    async save(){
      let res
      if(this.id){
        res = await this.$http.put(`rest/demands/${this.id}`,this.model)
      }
      else{
        res = await this.$http.post('/rest/demands',this.model)
      }
      this.$router.push('/square')
      this.$message({
        type:'success',
        message:'发布成功'
      })
    },
    async fetch(){
      const res = await this.$http.get(`rest/demands/${this.id}`)
      this.model = Object.assign({},this.model,res.data)
    },
    async deleteDemandInfo(id){
      const res = await this.$http.delete(`rest/demands/${this.id}`)
      this.$router.push('/me')
    }
  },
  created(){
    this.id && this.fetch() // 有 id 才执行 fetch() 获取数据
  },
}
</script>

