<template>
  <div class="goods-item">
    <div v-if="model" class="d-flex js-around flex-wrap pl-1">
      <router-link tag="div"
      class="mb-3"
      style="width:13.5rem;"
      v-for="item in model" 
      v-if="item.campus === campus"
      :key="item._id"
      :to="`/goods/${item._id}`">
        <img class="border-radius box-shadow" 
        :src="item.photo[0]" 
        style="height:12.5rem;">
        <h4 class="text-ellipsis mt-3 mb-3">{{item.name}}</h4>
        <div class="d-flex">
          <span class="flex-1 fs-sm ml-1 price">￥{{item.price}}</span>
          <span v-if="campus===0" class="text-grey-1 fs-sm mr-3">
            <i class="el-icon-location-outline"></i>宋跳
          </span>
          <span v-if="campus===1" class="text-grey-1 fs-sm mr-3">
            <i class="el-icon-location-outline"></i>苍梧
          </span>
          <span v-if="campus===2" class="text-grey-1 fs-sm mr-3">
            <i class="el-icon-location-outline"></i>通灌
          </span>
          <!-- <span class="text-grey-1 fs-sm mr-3">
            <i class="el-icon-location-outline"></i>{{item.campus}}
          </span> -->
          <span class="text-grey-1 fs-sm mr-3">{{item.createdAt | date}}</span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  props:{
    campus:{
      type:Number,
      default:0
    },
  },
  data(){
    return {
      model:null,
    }
  },
  methods:{
    async fetch(){
      // const res = await this.$http.get('/rest/goods')
      const res = await this.$http.get('/goods')
      this.model = res.data
    }
  },
  created(){
    this.fetch()
  }
}
</script>

