<template>
  <div>
    <nav-bar>
      <router-link tag="div" to="" slot="left"><span @click="$router.back(-1)">取消</span></router-link>
      <div slot="center">编辑资料</div>
      <div slot="right"></div>
    </nav-bar>
    <el-form ref="formRef" 
    @submit.native.prevent="save"
    class="bg-white auto-hight p-2" 
    style="height:90vh;"
    :model="model" 
    label-width="80px">
      <h2 class="text-center text-primary pb-3">个人信息</h2>
      <el-form-item label="用户名">
        <el-input v-model="model.username"></el-input>
      </el-form-item>
      <el-form-item label="手机">
        <el-input v-model="model.tel"></el-input>
      </el-form-item>
      <el-form-item label="微信">
        <el-input v-model="model.wechat"></el-input>
      </el-form-item>
      <el-form-item label="QQ">
        <el-input v-model="model.qq"></el-input>
      </el-form-item>
      <el-form-item prop="campus" label="所在校区">
        <el-select v-model="model.campus" placeholder="请选择您所在的校区">
          <el-option label="宋跳校区" value=0></el-option>
          <el-option label="苍梧校区" value=1></el-option>
          <el-option label="通灌校区" value=2></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">确认修改</el-button>
        <!-- <el-button @click="resetForm('formRef')">重置</el-button> -->
        <!-- <el-button>取消</el-button> -->
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  components: {
    NavBar
  },
  props:{
    id:{required:true}
  },
  data(){
    return{
      model:{
        username:''
      }
    }
  },
  methods:{
    async fetch(){
      const res = await this.$http.get(`/rest/users/${this.id}`)
      this.model = res.data
      // console.log(this.model)
    },
    async save() {
      const res = await this.$http.put(`/rest/users/${this.id}`,this.model)
      // 保存后自动刷新页面
      this.$router.go(-1); 
      // this.$router.push('/')
      this.$message({
        type:'success',
        message:'修改成功'
      })
    },
  },
  created(){
    this.fetch()
  }
}
</script>

