<template>
  <div class="setting">
    <nav-bar>
      <router-link tag='div' to='' slot="left"><i @click="$router.back(-1)" class="el-icon-arrow-left"></i></router-link>
      <div slot="center">设置</div>
      <div slot="right"></div>
    </nav-bar>
    <div class="d-flex p-3 bg-white">
      <router-link :to="`/avatar/${model._id}`">
        <img :src="model.avatar" style="height:5rem;border-radius:50%;">
      </router-link>
      <div class="d-flex flex-column flex-1 mt-3 pt-1 ml-3">
        <strong class="text-space fs-xl pb-2">{{model.username}}</strong>
        <div class="text-grey-1"><i class="el-icon-mobile-phone"></i> {{model.tel}}</div>
      </div>
      <el-button type="text" plain @click="editInfo">编辑</el-button>
    </div>
    <div v-if="model.qq || model.wechat || model.campus" class="qq bg-white height-1 pt-3 mt-3 d-flex jc-around">
      <div v-if="model.qq"><i class="iconfont icon-qq"></i> {{model.qq}}</div>
      <div v-if="model.wechat"><i class="iconfont icon-weixin"></i> {{model.wechat}}</div>
      <div v-if="model.campus"> 
        <div v-if="model.campus==='0'"><i class="el-icon-location-outline"></i> 宋跳校区</div>
        <div v-if="model.campus==='1'"><i class="el-icon-location-outline"></i> 苍梧校区</div>
        <div v-if="model.campus==='2'"><i class="el-icon-location-outline"></i> 通灌校区</div>
      </div>
    </div>
    <div class="logout mt-3 pt-2 bg-white height text-center">
      <el-button type="text" class="price" @click.native="logout">退出登录</el-button>
    </div>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  components: {
    NavBar
  },
  data(){
    return{
      model:{
        avatar:'',
        _id:'',
      },
    }
  },
  methods:{
    async fetch(){
      const res = await this.$http.get('/user')
      this.model = res.data
    },
    editInfo(){
      this.$router.push(`/userinfo/${this.model._id}`)
    },
    logout(){
      localStorage.clear()
      this.$router.go(0)
    }
  },
  created(){
    this.fetch()
  }
}
</script>

