<template>
  <div class="myrelease">
    <div v-if="isData">
      <router-link tag="div" 
      v-for="item in data" 
      :to="`/release/edit/${item._id}`"
      class="d-flex mb-2 p-2">
        <img class="border-radius box-shadow mr-3" 
        :src="item.photo[0]" 
        style="height:8rem;">
        <div class="pl-2 d-flex flex-column jc-between" style="width:18rem;">
          <div class="fs-xl pr-3 text-ellipsis goodsName mt-1">{{item.name}}</div>
          <div class="mt-3">
            <div class="fs-lg price">￥{{item.price}}</div>
            <div class="mt-3">
              <div v-if="item.campus===0" class="text-grey-1 fs-sm">
                <i class="el-icon-location-outline"></i>宋跳校区
              </div>
              <div v-if="item.campus===1" class="text-grey-1 fs-sm">
                <i class="el-icon-location-outline"></i>苍梧校区
              </div>
              <div v-if="item.campus===2" class="text-grey-1 fs-sm">
                <i class="el-icon-location-outline"></i>通灌校区
              </div>
            </div>
            <div class="text-grey-1 fs-sm mt-1 ml-1">{{item.createdAt | date}}</div>
          </div>
        </div>
      </router-link>
    </div>
    <div v-else>
      <div class="fs-lg p-2">暂时没有发布二手物品，立即去
        <router-link to="/release" tag="span"><el-link type="primary">发布</el-link></router-link>
      </div>
    </div>
    <div class="height"></div>
  </div>
</template>

<script>
export default {
  props:{
    id:{
      type:String,
      required:true
    },
    username:{
      type:String,
      required:true
    }
  },
  computed:{
    isData(){
      return this.data.length != 0
    }
  },
  data(){
    return{
      model:[],
      data:[]
    }
  },
  methods:{ 
    async fetch(){
      const res = await this.$http.get(`/goods`)
      this.model = res.data
      for(let item of this.model){
        if(item.userId.username === this.username){
          this.data.push(item)
        }
      }
      // console.log(this.data)
    }
  },
  created(){
    this.fetch()
  }
}
</script>

