<template>
  <div class="release">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center">江海大二手交易平台</div>
      <div slot="right"></div>
    </nav-bar>
    <el-form class="bg-white m-2 p-2"
    ref="formRef" 
    :model="model" 
    label-width="80px"
    @submit.native.prevent="save">
      <h2 class="text-center text-primary p-3">{{id ? "编 辑" : "发 布"}} 二 手 物 品</h2>
      <el-form-item prop="name" label="物品名称">
        <el-input placeholder="很重要，让别人一眼就能找到" 
         v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item prop="type" label="物品类型">
        <el-select v-model="model.type" multiple filterable  placeholder="请选择物品类型" >
          <el-option  v-for="item in types"
            :key="item._id"
            :label="item.name"
            :value="item._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="campus" label="所在校区">
        <el-select v-model="model.campus" placeholder="请选择您所在的校区">
          <el-option label="宋跳校区" value=0></el-option>
          <el-option label="苍梧校区" value=1></el-option>
          <el-option label="通灌校区" value=2></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="date" label="购入时间">
        <el-date-picker type="date" placeholder="选择日期" v-model="model.date" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item prop="originalPrice" label="购入价格">
        <el-input placeholder="如忘记，可填一下大概价格" v-model="model.originalPrice"></el-input>
      </el-form-item>
      <el-form-item label="物品图片">
        <el-upload
          v-model="model.photo"
          multiple
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
          :on-success="afterUpload"
          >
          <!-- <div v-if="model.photo" v-for="item in model.photo">
            <img :src="item">
          </div> -->
          <!-- <i v-else class="el-icon-plus"></i> -->
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>
      <el-form-item prop="description" label="物品描述">
        <el-input placeholder="很重要，别人决定买不买，就看这里写得详细与否" 
        type="textarea" 
        :rows="5" 
        v-model="model.description">
        </el-input>
      </el-form-item>
      <el-form-item prop="price" label="卖出价格">
        <el-input placeholder="说吧，想卖多少钱" v-model="model.price"></el-input>
      </el-form-item>
      <el-form-item prop="tel" label="联系方式">
        <el-input placeholder="留下联系方式，坐等收钱" v-model="model.tel"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" native-type="submit">{{id ? "保存" : "发布"}}</el-button>
        <el-button size="small" type="warning" @click="resetForm('formRef')">重置</el-button>
        <el-button v-if="id" size="small" type="danger" @click="deleteReleaseInfo">删除</el-button>
        <el-button v-if="id" size="small" type="info" @click="$router.back(-1)">取消</el-button>
      </el-form-item>
    </el-form>
    <div class="height"></div>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  name: 'Release',
  props:{
    id:{
      type:String
    }
  },
  components: {
    NavBar
  },
  data(){
    return {
      model: {
          name: '',
          type: [],
          campus: '',
          date: '',
          originalPrice: '',
          description: '',
          price: '',
          tel: '',
          photo:[]
        },
        dialogImageUrl: '',
        dialogVisible: false,
        types:null,
        /* options:[],
        selectedOptions: [],
        defaultParams: {
            label: 'name',
            value: '_id',
            children: 'parent'
        } */
    }
  },
  computed:{
  },
  methods: {
    // 将后台返回的图片地址保存下来
    async afterUpload(res){
      this.model.photo.push(res.url)
    },
    // 删除选中的图片
    handleRemove(file, fileList) {
      // console.log(file, fileList);
      const text = file.response.url
      const arr = this.model.photo
      for(let i = 0; i < arr.length; i++) {
          if (text == arr[i]) {
          arr.splice(i,1);
          }
        }
    },
    // 展示上传的图片
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 获取分类列表的方法
    async fetchCategories(){
      const res = await this.$http.get('/rest/categories')
      this.types = res.data
      // this.options = res.data
      // this.parents = this.types.filter(n=>!n.parent)
      // this.children = this.types.filter(n=>n.parent)
    },
    // 保存发布的二手物品
    async save() {
      let res
      // 如果有id，说明是要修改二手物品
      if(this.id){
        res = await this.$http.put(`rest/goods/${this.id}`,this.model)
      }
      // 如果没有id，说明是要新建二手物品
      else{
        res = await this.$http.post('/rest/goods',this.model)
      }
      // const res = await this.$http.post('/rest/goods',this.model)
      // 保存后自动刷新页面
      // this.$router.go(0); 
      this.$router.push('/')
      this.$message({
        type:'success',
        message:'发布成功'
      })
    },
    // 重置表单
    resetForm(formRef) {
      this.$refs[formRef].resetFields();
    },
    async fetch(){
      const res = await this.$http.get(`rest/goods/${this.id}`)
      this.model = Object.assign({},this.model,res.data)
    },
    async deleteReleaseInfo(id){
      const res = await this.$http.delete(`rest/goods/${this.id}`)
      this.$router.push('/me')
    }
  },
  created(){
    this.fetchCategories()
    this.id && this.fetch() // 有 id 才执行 fetch() 获取数据
  },
}
</script>

