<template>
  <div class="login-container">
    <nav-bar>
      <div slot="left"><img src="../assets/logo_mini.jpg"></div>
      <div slot="center">江海大二手交易平台</div>
      <div slot="right"></div>
    </nav-bar>
    <el-card header="" class="m-2 login-card text-center fs-xl">
      <!-- ative表示监听表单的原生事件，prevent表示阻止默认跳转 -->
      <el-form
       :rules="rules" 
       @submit.native.prevent="save" 
       ref="formRef" 
       :model="model"
       :label-position="labelPosition">
       <h2 class="text-center text-primary pb-2">用 户 注 册</h2>
        <el-form-item prop="username" label="用户名">
          <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item prop="tel" label="手机号码">
          <el-input v-model="model.tel"></el-input>
        </el-form-item>
        <el-form-item prop="password" label="密码">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item prop="password2" label="确认密码">
          <el-input type="password" v-model="model.password2"></el-input>
        </el-form-item>
        <el-form-item class="pt-3">
          <el-button type="primary" native-type="submit">注册</el-button>
          <el-button @click="resetForm('formRef')">重置</el-button>
        </el-form-item>
        <el-form-item>
          <span>已有账号？ <router-link class="text-primary" tag='span' to="/login"> 立即登录</router-link></span>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import NavBar from '../components/NavBar'
export default {
  components: {
    NavBar
  },
  data(){
    // 验证确认密码的规则
    let confirmpasswordCheck = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('密码是必须的'))
      } else if (value !== this.model.password) {
        return callback(new Error('两次输入的密码不一致'))
      } else {
        return callback()
      }
    }
    // 验证手机号的规则
      const checkMobile = (rule,value,cb) => {
        // 验证手机号的正则表达式
        const regMobile = /^1(3[0-9]|4[5,7]|5[0,1,2,3,5,6,7,8,9]|6[2,5,6,7]|7[0,1,7,8]|8[0-9]|9[1,8,9])\d{8}$/
        if(regMobile.test(value)){
          // 合法的手机号
          return cb()
        }
        cb(new Error('请输入合法的手机号'))
      }
    return{
      model:{},
      labelPosition: 'right',
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' }
        ],
        tel:[
            { required: true, message: '请输入手机号码', trigger: 'blur' },
             { validator: checkMobile, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ],
        password2: [
          {
            required: true,
            validator: confirmpasswordCheck,
            trigger: 'blur'
          }
        ],
      }
    }
  },
  methods:{
    async save(){
      const res = await this.$http.post('/register',this.model)
      this.$router.push('/login')
      this.$message({
        type:'success',
        message:'注册成功'
      })
    },
    resetForm(formRef) {
        this.$refs[formRef].resetFields();
    },
  }
}
</script>

<style>
  .login-card{
    margin: 1rem auto;
  }
</style>