<template>
  <div class="report-info">
    <div v-for="item in model" class="mb-2">
      <el-card>
        <h2>{{item.reportContent}}</h2>
        <el-divider class="mt-3 mb-3"></el-divider>
        <div class="d-flex">
          <img :src="item.reportID.avatar" style="height:3rem;" class="border-radius">
          <div class="d-flex flex-column ml-2 mt-1 flex-1">
            <strong class="mb-2">{{item.reportID.username}}</strong>
            <!-- <div class="text-grey-1">{{item.reportID.tel}}</div> -->
            <div class="text-grey-1" v-if="isLogin"><i class="el-icon-mobile-phone"></i>{{item.reportID.tel}}</div>
            <router-link
            tag='span' 
            to="/login"
            class="text-primary"
            v-else-if="!isLogin">登陆后查看联系方式
            </router-link>
          </div>
          <div class="mt-3">
            <span v-if="item.reportID.campus === '0'"><i class="el-icon-location-outline"></i>宋跳校区</span>
            <span v-if="item.reportID.campus === '1'"><i class="el-icon-location-outline"></i>苍梧校区</span>
            <span v-if="item.reportID.campus === '2'"><i class="el-icon-location-outline"></i>通灌校区</span>
          </div>
        </div>
      </el-card>
    </div>
    <div class="height"></div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      model:null
    }
  },
  computed:{
    isLogin(){
      return localStorage.token ? true : false
    }
  },
  methods:{
    async fetch(){
      const res = await this.$http.get('/reports')
      this.model = res.data
    }
  },
  created(){
    this.fetch()
  }
}
</script>

