<template>
  <div class="demand-info">
    <div v-for="item in model" class="mb-2">
      <el-card>
        <strong class="fs-xl">{{item.name}}</strong>
        <div class="mt-3 mb-3">{{item.description}}</div>
        <div class="d-flex">
          <div class="text-grey-1 flex-1">预期价格：<span class="price">￥{{item.price}}</span> </div>
          <div class="text-grey-1 fs-sm">更新时间：{{item.createdAt | date}}</div>
        </div>
        <el-divider class="mt-3 mb-3"></el-divider>
        <div class="d-flex">
          <img :src="item.userId.avatar" style="height:3rem;" class="border-radius">
          <div class="d-flex flex-column ml-2 mt-1 flex-1">
            <strong class="mb-2">{{item.userId.username}}</strong>
            <!-- <div class="text-grey-1">{{item.userId.tel}}</div> -->
            <div class="text-grey-1" v-if="isLogin"><i class="el-icon-mobile-phone"></i>{{item.userId.tel}}</div>
            <router-link
            tag='span' 
            to="/login"
            class="text-primary"
            v-else-if="!isLogin">登陆后查看联系方式
            </router-link>
          </div>
          <div class="mt-3">
            <span v-if="item.userId.campus === '0'"><i class="el-icon-location-outline"></i>宋跳校区</span>
            <span v-if="item.userId.campus === '1'"><i class="el-icon-location-outline"></i>苍梧校区</span>
            <span v-if="item.userId.campus === '2'"><i class="el-icon-location-outline"></i>通灌校区</span>
          </div>
        </div>
      </el-card>
    </div>
    <div class="height"></div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      model:null
    }
  },
  computed:{
    isLogin(){
      return localStorage.token ? true : false
    }
  },
  methods:{
    async fetch(){
      const res = await this.$http.get('/demands')
      this.model = res.data
      // console.log(this.model)
    }
  },
  created(){
    this.fetch()
  }
}
</script>

