<template>
  <!-- 导航栏 -->
    <div class="tabbar d-flex jc-around fs-xl border-top">
      <div v-for="(item,index) in tabItem" 
      class="tab-item" 
      :class="{active:index===currentIndex}"
      @click="addClass(index)">
        <router-link tag="div" :to="item.path">
          <i class="pr-1" :class="item.icon"></i>{{item.name}}
        </router-link>
      </div>
    </div>
</template>

<script>
export default {
  data(){
    return{
      tabItem:[
        { icon:'el-icon-house', name:'首页' , path:'/'},
        { icon:'el-icon-circle-plus-outline', name:'发布' , path:'/release'},
        { icon:'el-icon-position', name:'广场' , path:'/square'},
        { icon:'el-icon-help', name:'求购' , path:'/demand'},
        { icon:'el-icon-user', name:'我的' , path:'/me'},
      ],
      currentIndex:0
    }
  },
  methods:{
    addClass(index){
      this.currentIndex = index
    }
  }
}
</script>

<style lang="scss">
  @import '../assets/scss/variables.scss';
  .tabbar {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 3.7692rem;
    line-height: 3.7692rem;
    background-color: map-get($map: $colors, $key: 'white');
    .tab-item {
        &.active {
            color: map-get($map: $colors, $key: 'primary');
        }
    }
  }
</style>